 import Vue from 'vue'
import Vuex from 'vuex'

//use Vuex
Vue.use(Vuex)

//create Vuex
const store = new Vuex.Store({
  //
  state:{
    nickNameFlag:false,
    logoutFlag:false,
    nickName:'',
    num:100,
    headerImgData:''
  },
  //getter,state, this.$store.getters.filterNum
  getters:{
    filterNum(state){
      return state.num >=120 ? 120 : state.num
    }
  },


  //state , mutation ,
  mutations:{
    addIncrement(state,payload){
      //statestate
      state.num +=5

    },
    subIncrement(state){
      state.num -=5
    }
  },
  //action, ,context
  actions:{
    // addAction(context){
    //   console.log(context)
    //   setTimeout(()=>{
    //     context.commit('addIncrement',{n:5})

    //     //dispatchsubAction,
    //     context.dispatch('subAction',{name:'yang',age:11})
    //   },2000)
    // },

    //
    addAction({commit,dispatch}){
      setTimeout(()=>{
        commit('addIncrement',{n:5})
        dispatch('subAction',{name:'yang',age:11})
      })
    },


    //obj
    subAction(context,obj){
      console.log('hi sub !')
      console.log(obj)
    }
  }
})

//export store
export default store



