<template>
  <div>
    <el-row>
      <el-col :span="24" >
        <el-breadcrumb separator-class="el-icon-arrow-right  nav2">
          <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
          <el-breadcrumb-item :to="{ path: '/regist'}"></el-breadcrumb-item>
          <el-breadcrumb-item :to="{ path: '/help'}"></el-breadcrumb-item>
        </el-breadcrumb>
        <ul>
          <li>
            <router-link to="/">
              <img class="left nav1" alt="" style="width: 10%; height: 10%" src='../../static/img/brand.png'>
            </router-link>

          </li>
          <li>


            <p class="left nav2"></p>
          </li>
          <li>
            <router-link to="/login">
              <el-button class="right nav3" type="info" plain></el-button>
            </router-link>
          </li>
          <li>
            <span class="right nav4"></span>
          </li>
        </ul>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24">
        <div class='shadow'></div>
      </el-col>
    </el-row>
    <el-row type="flex" class="row-bg">
      <el-col :span="12" offset="4">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
          <el-form-item label="" prop="name">
            <el-input v-model="ruleForm.name" @blur="checkName"></el-input>
            <span class="errmessage">{{ nameMsg}}</span>
          </el-form-item>

          <el-form-item label=":" prop="buyerPhone" required>
                  <el-input v-model="ruleForm.buyerPhone"></el-input>
          </el-form-item>
          <el-form-item label="Email:" prop="buyerEmail" required>
                  <el-input v-model="ruleForm.buyerEmail"></el-input>
          </el-form-item>


          <el-form-item label="" prop="pass" required>
            <el-input type="password" v-model="ruleForm.pass" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="checkPass" required>
            <el-input type="password" v-model="ruleForm.checkPass" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item prop='checked' required >
            <el-checkbox v-model='ruleForm.checked'  :disabled="isDisabled">
              <span>
                

              </span>
            </el-checkbox>
            <!-- <el-button type="text" @click="dialogFormVisible = true"></el-button> -->
            <el-button type="text" @click="TimerFlag"></el-button>

            <el-dialog title="" :visible.sync="dialogFormVisible">
              <el-form :model="form">
                    <div class='contents'>
                       <h2></h2>

                          <h3></h3>

                          

                          <h3></h3>

                          

                          

                          

                          

                          

                          

                          

                          QQ

                          

                          

                          



                          <h3> </h3>

                          

                          

                          ***http://www.******.com/bbs/

                          

                          

                          


                          <h3></h3>

                          

                          ID

                           [] 

                          

                          

                          



                          <h3> </h3>

                          

                          

                                                                                                                             

                                                                                                                            200744

                                                                                  
                    </div>
              </el-form>
              <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false"> </el-button>
                <el-button type="primary" @click="ruleConfirm" :disabled="isDisabled1">{{buttonName}}</el-button>
                 <!-- <el-button type="primary" @click=" dialogFormVisible =false;checked=true"> </el-button> -->
              </div>
            </el-dialog>

          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
            <el-button @click="resetForm('ruleForm')"></el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>




    <el-button plain class="btn1"><router-link to='/help'><span id="aa"></span></router-link></el-button>
    <Foot></Foot>


  </div>

</template>

<script>
  import Foot from '@/components/Foot'
  import axios from 'axios'
  export default {
    name: 'Regist',
    data() {
      var checkPhone = (rule, value, callback) => {
          const phoneReg = /^1[3|4|5|7|8][0-9]{9}$/
          if (!value) {
            return callback(new Error(''))
          }
          setTimeout(() => {
            // Number.isIntegeres6,
            // +

            if (!Number.isInteger(+value)) {
              callback(new Error(''))
            } else {
              if (phoneReg.test(value)) {
                callback()
              } else {
                callback(new Error(''))
              }
            }
          }, 100)
        }
        var checkEmail = (rule, value, callback) => {
          const mailReg = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/
          if (!value) {
            return callback(new Error(''))
          }
          setTimeout(() => {
            if (mailReg.test(value)) {
              callback()
            } else {
              callback(new Error(''))
            }
          }, 100)
        }
      var validatePass = (rule, value, callback) => {
              if (value === '') {
                callback(new Error(''));
              } else {
                if (this.ruleForm.checkPass !== '') {
                  this.$refs.ruleForm.validateField('checkPass');
                }
                callback();
              }
            };
            var validatePass2 = (rule, value, callback) => {
              if (value === '') {
                callback(new Error(''));
              } else if (value !== this.ruleForm.pass) {
                callback(new Error('!'));
              } else {
                callback();
              }
            };
      return {
        nameMsg:'',
        buttonName: "",
        time: 10,
        isDisabled: true,
        isDisabled1: false,
        dialogFormVisible: false,
        form: {
                   name: ''
              },
        formLabelWidth: '30%',
        dialogVisible: false,
        ruleForm: {
          checked:false,
          name: '',
          region: '',
          date1: '',
          date2: '',
          delivery: false,
          type: [],
          resource: '',
          desc: '',
          pass: '',
          checkPass: '',
          buyerPhone: '',
          buyerEmail: ''
        },
        rules: {
          buyerPhone: [
                { validator: checkPhone, trigger: 'blur' }
              ],
              buyerEmail: [
                { validator: checkEmail, trigger: 'blur'  }
              ],
          pass: [{
            validator: validatePass,
            trigger: 'blur'
          }],
          checkPass: [{
            validator: validatePass2,
            trigger: 'blur'
          }],
          checked:[{
            required:true,
            trigger:'blur'
          }],
          name: [{
              required: true,
              message: '',
              trigger: 'blur'
            },
            {
              min: 2,
              max: 8,
              message: '2  8 ',
              trigger: 'blur'
            }
          ],
          region: [{
            required: true,
            message: '',
            trigger: 'change'
          }],
          date1: [{
            type: 'date',
            required: true,
            message: '',
            trigger: 'change'
          }],
          date2: [{
            type: 'date',
            required: true,
            message: '',
            trigger: 'change'
          }],
          type: [{
            type: 'array',
            required: true,
            message: '',
            trigger: 'change'
          }],
          resource: [{
            required: true,
            message: '',
            trigger: 'change'
          }],
          desc: [{
            required: true,
            message: '',
            trigger: 'blur'
          }]
        }


      }
    },
    methods: {
      //
      checkName(){
        axios.post('/api/admins/checkName', {
          userName: this.ruleForm.name
        }).then((result) => {
          let res = result.data
          console.log(res.msg)
          this.nameMsg = res.msg


        })
      },
      TimerFlag(){
        this.dialogFormVisible = true
        let me = this;
        me.isDisabled1 = true;
        let interval = window.setInterval(function() {
        	me.buttonName = '' + me.time + '';
        	--me.time;
        	if(me.time < 0) {
        		me.buttonName = "";
        		me.time = 10;
        		me.isDisabled1 = false;
        		window.clearInterval(interval);
        	}
        }, 1000);
      },
      ruleConfirm(){

        this.dialogFormVisible =false;
        this.isDisabled = false;
        this.ruleForm.checked=true
      }
      ,
       handleClose(done) {
              this.$confirm('')
                .then(_ => {
                  done();
                })
                .catch(_ => {});
            },
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            var flag = this.ruleForm.checked
            if(flag){

            let data = {
              'userId':10000,
              'userName':this.ruleForm.name,
              'buyerPhone':this.ruleForm.buyerPhone,
              'buyerEmail':this.ruleForm.buyerEmail,
              'userPwd':this.ruleForm.pass
            }
            axios.post('/api/admins/userRegist',data).then((result)=>{
              let res = result.data
              if(res.status ==0 ){
                console.log('')
                this.open()
              }else{
                console.log('')
              }
            })
            }else{
              this.rec()
            }
          } else {
            console.log('error submit!!');
            return false;
          }
        })
      },
      rec(){
        var mymessage=confirm("");
          	if(mymessage==true)
          	{
          		this.TimerFlag()
          	}
          	else if(mymessage==false)
          	{
          		 this.$router.push('/regist')

        }
      },
      open() {
              this.$alert('', {
                confirmButtonText: '',
                callback: action => {
                  this.$message({
                    type: 'info',
                    //message: `action: ${ action }`
                    message:''
                  });
                }
              });
      },

      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    },
    components:{
      Foot
    },
    mounted:
      function(){
      		//
      		let w = document.documentElement.offsetWidth || document.body.offsetWidth;
      		if(w < 1000){
      			this.show = false;
      		}
      }
  }
</script>

<style scoped>
  .nav1 {
    margin-left: 5%;

  }
  .errmessage{
    color:red;
    backgrond:yellow;
  }
  .nav2 {
    margin-left: 3%;
    width: 25%;
  }

  .nav3 {
    margin-top: 3em;
    margin-right: 1%;

  }

  .nav4 {
    margin-top: 3.2em;
    display: block;
    float: right;
  }

  .nav5 {
    height: 40px;
    border: 1px solid red;
  }

  p {
    font-size: 0.5rem
  }
  .row-bg{
    height:1500px;
  }
  .btn1{
   font-size:20px;
    margin:0 atuo;
    line-height: 24px;
    width:20xp;
    position: fixed;bottom: 50%;right: 1px;
  }
  #aa{
    writing-mode: vertical-lr;
  }
</style>



