        <router-link v-if="!this.$store.state.nickNameFlag" class="nav1 el-icon-s-custom" to="/login"></router-link>

        <span class="nav1 el-icon-s-custom" v-if="this.$store.state.nickNameFlag">{{ this.$store.state.nickName }}</span>

