            <el-submenu index="/buddhist" style="width:10%;">
              <template slot="title"></template>
              <el-menu-item index="/buddhist/part5"></el-menu-item>
              <el-menu-item index="/buddhist/part6"></el-menu-item>
              <el-menu-item index="/buddhist/part7"></el-menu-item>
