</template>

<script>
  import axios from 'axios'
  export default {
    name: 'Head',
    data() {
      return {


      }
    },
    computed:{

    },
    created(){
      this.layout()
    },
    methods: {
      layout(){
        var w = document.documentElement.scrollWidth;
        if(w > 1201){
          window.document.documentElement.style.fontSize = "75px";
        }
      },
      handleSelect(key, keyPath) {
        console.log(key, keyPath);
      },
      logout(){
        axios.post('/api/admins/logout').then((result) =>{
          let res = result.data
          this.$store.state.nickName = ''
          this.$store.state.nickNameFlag = false
        })
      },
      checkLogin(){
        axios.post('/api/admins/checkLogin').then((result)=>{
          let res = result.data
          console.log(res)
          this.$store.state.nickName = res.result
        })
      },
    },
    mounted: function() {

      this.checkLogin()

      //
      let w = document.documentElement.offsetWidth || document.body.offsetWidth;
      if (w < 1000) {
        this.show = false;
      }

    }


  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .imgbox{
